import BaseComp from './BaseComp.vue';
import BaseButton from './BaseButton.vue';

const install = function (Vue) {
    // 全局注册组件
    Vue.component('BaseComp', BaseComp);
    Vue.component('BaseButton', BaseButton);
}

export {
    install,
    BaseComp,
    BaseButton
}

export default {
    install
}
